import React from 'react';
import TextField from 'material-ui/TextField';
import RaisedButton from 'material-ui/RaisedButton';
import Snackbar from '../common/dialog/snackbars';
import Alert from '../common/dialog/alert';
import validate from '../../js/valid.js';


class TextFieldExampleSimple extends React.Component {
	constructor(props) {
		super(props);
		// this.state = {
		// 	name: '',
		// 	namevalid: false,
		// 	nameerror: '',
		// 	email: '',
		// 	emailvalid: false,
		// 	emailerror: '',
		// 	phone: '',
		// 	phonevalid: false,
		// 	phoneerror: '',
		// 	content: '',
		// 	contentvalid: false,
		// 	contenterror: ''
		// };
		this.state = {
			name: {
				value: '',
				valid: false,
				error: ''
			},
			email: {
				value: '',
				valid: false,
				error: ''
			},
			phone: {
				value: '',
				valid: false,
				error: ''
			},
			content: {
				value: '',
				valid: false,
				error: ''
			}
		};
	}
	handleChange(id, event) {
		var value = event.target.value;
		var name = event.target.name;
		this.setValidData(id, name, value);
	}
	setValidData(id, name, value, func) {
		var newState = {};
		validate(name, value, function(response) {
			newState[id] = {
				value: value,
				valid: response.valid,
				error: response.error
			};
		}.bind(this))
		this.setState(newState);
		if (func != undefined) func();
	}
	handleTouchTap() {
		if (this.state.name.value === "") {
			// this.refs.alert.handleOpen(this.state.name.error);
			this.setValidData("name", "text", this.state.name.value);
		} else if (this.state.phone.value === "") {
			// this.refs.alert.handleOpen(this.state.phone.error);
			this.setValidData("phone", "phone", this.state.phone.value);
		} else if (this.state.email.value === "") {
			// this.refs.alert.handleOpen(this.state.email.error);
			this.setValidData("email", "email", this.state.email.value);
		} else if (this.state.content.value === "") {
			// this.refs.alert.handleOpen(this.state.content.error);
			this.setValidData("content", "text", this.state.content.value);
		} else if (this.state.name.valid && this.state.email.valid && this.state.phone.valid && this.state.content.valid) {
			this.refs.snackbar.handleTouchTap("Save successful!");
		}
	}
	render() {
		return (<div>
    <TextField
      hintText = "Name"
      floatingLabelText="Your Name"
      fullWidth={true}
      id="name"
      ref="name"
      name="text"
      errorText={this.state.name.valid?'':this.state.name.error}
      onChange={this.handleChange.bind(this,"name")}
    />
    <TextField
      hintText = "Phone"
      floatingLabelText="Your Phone"
      fullWidth={true}
      id="phone"
      ref="phone"
      name="phone"
      errorText={this.state.phone.valid?'':this.state.phone.error}
      onChange={this.handleChange.bind(this,"phone")}
    />
    <TextField
      hintText = "E-mail"
      floatingLabelText="Your E-mail"
      fullWidth={true}
      id="email"
      ref="email"
      name="email"
      errorText={this.state.email.valid?'':this.state.email.error}
      onChange={this.handleChange.bind(this,"email")}
    />
    <TextField
      hintText="Content"
      floatingLabelText="Your Need"
      fullWidth={true}
      multiLine = {true}
      rows={4}
      id="content"
      ref="content"
      name="text"
      errorText={this.state.content.valid?'':this.state.content.error}
      onChange={this.handleChange.bind(this,"content")}
    />
        <RaisedButton 
        label = "Save"
        fullWidth = {true}
        primary = {true}
        onClick={this.handleTouchTap.bind(this)}
        />
        <Alert ref="alert" />
        <Snackbar ref="snackbar" />
  </div>)
	}
}

export default TextFieldExampleSimple;


