import React from 'react';
import AppBar from 'material-ui/AppBar';
import Drawer from './drawer';

const style = {
    position: 'fixed',
    top: 0
}
const titleStyle = {
    textAlign: 'center'
}
class AppBarExampleIcon extends React.Component {
    render() {
        return (<div>
    		<div className="line60"></div>
        	<AppBar 
        	title = "Icursoft Cases"
            titleStyle = { titleStyle }
            style={style}
            showMenuIconButton={true}
            onTitleTouchTap = { this.handleTapTitle.bind(this) } 
            onLeftIconButtonTouchTap = { this.handleLeftTapTitle.bind(this) } 
            iconClassNameRight = "muidocs-icon-navigation-expand-more" />
            <Drawer ref="drawer"/>
            </div>)
    }
    handleTapTitle() {
        alert(1)
    }
    handleLeftTapTitle() {
        this.refs.drawer.handleToggle();
    }
}

export default AppBarExampleIcon;


