import React from 'react';
import Drawer from 'material-ui/Drawer';
import Divider from 'material-ui/Divider';
import {
  List,
  ListItem
} from 'material-ui/List';
import ContentInbox from 'material-ui/svg-icons/content/inbox';
import ContentDrafts from 'material-ui/svg-icons/content/drafts';
import ActionGrade from 'material-ui/svg-icons/action/grade';
import ContentSend from 'material-ui/svg-icons/content/send';

export default class DrawerUndockedExample extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      open: false
    };
  }

  handleToggle = () => this.setState({
    open: !this.state.open
  });

  handleClose = () => this.setState({
    open: false
  });

  render() {
    return (
      <div>
        <Drawer
          docked={false}
          width={250}
          open={this.state.open}
          onRequestChange={(open) => this.setState({open})}
        ><List>
            <ListItem primaryText="企业级信息化系统开发" leftIcon={<ContentInbox />} onClick={this.handleClose} />
            <Divider />
            <ListItem primaryText="微信开发/APP开发" leftIcon={<ContentDrafts />} onClick={this.handleClose} />
            <Divider />
            <ListItem primaryText="电商/高端网站建设" leftIcon={<ActionGrade />} onClick={this.handleClose} />
            <Divider />
            <ListItem primaryText="工业4.0/智能设备开发" leftIcon={<ContentSend />} onClick={this.handleClose} />
          </List>
        </Drawer>
      </div>
    );
  }
}


