import React from 'react';
import {
  Step,
  Stepper,
  StepLabel,
  StepContent,
} from 'material-ui/Stepper';
import RaisedButton from 'material-ui/RaisedButton';
import FlatButton from 'material-ui/FlatButton';
import Gridlist from './gridlist';


const style = {
  p: {
    fontSize: 12,
    lineHeight: 2
  },
  iconStyles: {
    marginRight: 24,
  }
}
class VerticalLinearStepper extends React.Component {

  state = {
    finished: false,
    stepIndex: 0,
  };

  handleNext = () => {
    const {
      stepIndex
    } = this.state;
    this.setState({
      stepIndex: stepIndex + 1,
      finished: stepIndex >= 3,
    });
  };

  handlePrev = () => {
    const {
      stepIndex
    } = this.state;
    if (stepIndex > 0) {
      this.setState({
        stepIndex: stepIndex - 1
      });
    }
  };

  renderStepActions(step) {
    const {
      stepIndex
    } = this.state;

    return (
      <div style={{margin: '12px 0'}}>
        <RaisedButton
          label={stepIndex === 3 ? 'Finish' : 'Next'}
          disableTouchRipple={true}
          disableFocusRipple={true}
          primary={true}
          onClick={this.handleNext}
          style={{marginRight: 12}}
        />
        {step > 0 && (
          <FlatButton
            label="Back"
            disabled={stepIndex === 0}
            disableTouchRipple={true}
            disableFocusRipple={true}
            onClick={this.handlePrev}
          />
        )}
      </div>
    );
  }

  render() {
    const {
      finished,
      stepIndex
    } = this.state;

    return (
      <div style={{maxWidth: 380, maxHeight: '100%', margin: 'auto'}}>
        <Stepper activeStep={stepIndex} orientation="vertical">
          <Step>
            <StepLabel>Our Culture</StepLabel>
            <StepContent style={style.p}>
              <br/>
              <p>天津光标科技有限公司是一家以积极、严谨、优质、高效为核心的互联网软件开发公司。
拥有国内顶级的设计、技术团队和多年互联网软件开发经验</p>
              {this.renderStepActions(0)}
            </StepContent>
          </Step>
          <Step >
            <StepLabel>Our Idea </StepLabel>
            <StepContent style={style.p}>
              <br/>
              <p>○&nbsp;光标在挑战中诞生，迎难不惧，逆风前行。
挑战已成为我们的一种习惯</p>
              <p>○&nbsp;一次次突破自我极限，一次次追求作品极致
我们的创新思维永无极限</p>
              <p>○&nbsp;光标存在的价值，是竭力为客户创造价值
最高的境界不止于纯粹的开发</p>
              {this.renderStepActions(1)}
            </StepContent>
          </Step>
          <Step>
            <StepLabel>Our Partner</StepLabel>
            <StepContent style={style.p}>
            <Gridlist/>
              {this.renderStepActions(2)}
            </StepContent>
          </Step>
          <Step>
            <StepLabel>Our address</StepLabel>
            <StepContent style={style.p}>
              <br/>
              <p>QQ&nbsp;：502126689/15300800</p>
              <p>Email：hr@icursoft.com/bd@icursoft.com</p>
              <p>Tel&nbsp;：400-825-7699/022-83709226</p>
              <p>ADD&nbsp;：天津市新产业园区华苑产业区（环外）海泰发展五道16号创新基地B-9号楼402室</p>
              {this.renderStepActions(3)}
            </StepContent>
          </Step>
        </Stepper>
        {finished && (
          <p style={{margin: '20px 0', textAlign: 'center'}}>
          <RaisedButton
            label="Again"
              onClick={(event) => {
                event.preventDefault();
                this.setState({stepIndex: 0, finished: false});
              }}
          />
          </p>
        )}
      </div>
    );
  }
}

export default VerticalLinearStepper;


