import React from 'react';
import {
  Link
} from 'react-router-dom';
import {
  List,
  ListItem
} from 'material-ui/List';
import Divider from 'material-ui/Divider';
import Avatar from 'material-ui/Avatar';
import {
  darkBlack
} from 'material-ui/styles/colors';
import Loading from '../common/loading';
class CaseList extends React.Component {
  constructor() {
    super();
    this.state = {
      datalist: [{
        img: 'http://www.material-ui.com/images/grid-list/00-52-29-429_640.jpg',
        title: 'Breakfast',
        author: 'jill111',
        content: 'BkrmadtyaKarki BkrmadtyaKarki BkrmadtyaKarki BkrmadtyaKarki BkrmadtyaKarki'
      }, {
        img: 'http://www.material-ui.com/images/grid-list/burger-827309_640.jpg',
        title: 'Tasty burger',
        author: 'pashminu',
        content: 'BkrmadtyaKarki BkrmadtyaKarki BkrmadtyaKarki BkrmadtyaKarki BkrmadtyaKarki'
      }, {
        img: 'http://www.material-ui.com/images/grid-list/camera-813814_640.jpg',
        title: 'Camera',
        author: 'Danson67',
        content: 'BkrmadtyaKarki BkrmadtyaKarki BkrmadtyaKarki BkrmadtyaKarki BkrmadtyaKarki'
      }, {
        img: 'http://www.material-ui.com/images/grid-list/morning-819362_640.jpg',
        title: 'Morning',
        author: 'fancycrave1',
        content: 'BkrmadtyaKarki BkrmadtyaKarki BkrmadtyaKarki BkrmadtyaKarki BkrmadtyaKarki'
      }, {
        img: 'http://www.material-ui.com/images/grid-list/hats-829509_640.jpg',
        title: 'Hats',
        author: 'Hans',
        content: 'BkrmadtyaKarki BkrmadtyaKarki BkrmadtyaKarki BkrmadtyaKarki BkrmadtyaKarki'
      }, {
        img: 'http://www.material-ui.com/images/grid-list/honey-823614_640.jpg',
        title: 'Honey',
        author: 'fancycravel',
        content: 'BkrmadtyaKarki BkrmadtyaKarki BkrmadtyaKarki BkrmadtyaKarki BkrmadtyaKarki'
      }, {
        img: 'http://www.material-ui.com/images/grid-list/vegetables-790022_640.jpg',
        title: 'Vegetables',
        author: 'jill111',
        content: 'BkrmadtyaKarki BkrmadtyaKarki BkrmadtyaKarki BkrmadtyaKarki BkrmadtyaKarki'
      }, {
        img: 'http://www.material-ui.com/images/grid-list/water-plant-821293_640.jpg',
        title: 'Water plant',
        author: 'BkrmadtyaKarki',
        content: 'BkrmadtyaKarki BkrmadtyaKarki BkrmadtyaKarki BkrmadtyaKarki BkrmadtyaKarki'
      }]
    }
  }

  componentDidMount() {
    this.getList();
  }
  getList() {

  }
  render() {
    var arrLi = [];
    this.state.datalist.forEach((item, index) => {
      arrLi.push(<Link to="/detail" key={index}>
        <ListItem
          leftAvatar={<Avatar src={item.img} />}
          primaryText={item.author}
          secondaryText={
            <p>
              <span style={{color: darkBlack}}>{item.author}</span> --&nbsp;
              {item.content}
            </p>
          }
          secondaryTextLines={2}
        />
        <Divider inset={false} />
        </Link>)
    })
    return (<div>
      <List>
       {arrLi}
      </List>
    { /*<Loading/>*/ }
      </div>)
  }
}
export default CaseList;


