var validate = function(type, value, func) {
	let response = {
		valid: false,
		error: ''
	}
	switch (type) {
		case 'text':
			response.valid = value.length >= 1;
			response.error = response.valid ? '' : 'Can not be empty';
			break;
		case 'email':
			response.valid = value.length >= 1;
			response.error = response.valid ? '' : 'Can not be empty';
			response.valid = value.match(/^([\w.%+-]+)@([\w-]+\.)+([\w]{2,})$/i);
			response.error = response.valid ? '' : 'Please fill in the correct e-mail';
			break;
		case 'phone':
			response.valid = value.length >= 1;
			response.error = response.valid ? '' : 'Can not be empty';
			response.valid = value.match(/^1\d{10}$/i);
			response.error = response.valid ? '' : 'Please fill in the correct phone';
			break;
		default:
			break;
	}
	if (func !== undefined) func(response);
}
export default validate;


